      <span>发表时间: {{this.info.add_time | Dateformat("YYYY年MM月DD日 HH:mm:ss")}}</span>
      <span class="mui-pull-right">点击: {{this.info.click}}次</span>
    </p>
    <hr>
    <router-view :imgs="imgs"></router-view>
    <div v-html="this.info.content"></div>
  </div>
</template>

<script>
import Dateformat from "@/filters/dateformat.js";
export default {
  props: ["info", "imgs"],
  filters: {
    Dateformat
  }
};
</script>

<style>
h4 {
  padding: 5px 0;
}
.newstitle img {
  width: 100%;
}
</style>
