<template>
    <div class="page">
        <!-- 图片详情 -->
        <DetailDemo :info="picInfo" :imgs="imgsInfo"></DetailDemo>
        <!-- 评论 -->
        <Comments :detailid="$route.params.id"></Comments>
    </div>
</template>

<script>
import axios from "axios";
import DetailDemo from "@/components/DetailDemo";
import Comments from "@/components/Comment";
export default {
  data() {
    return {
      picInfo: [],
      imgsInfo: []
    };
  },
  components: {
    Comments,
    DetailDemo
  },
  created() {
    axios({
      url: "http://www.liulongbin.top:3005/api/getnew/" + this.$route.params.id,
      method: "get"
    }).then(res => {
      this.picInfo = res.data.message[0];
    });
    axios({
      url:
        "http://www.liulongbin.top:3005/api/getthumimages/" +
        this.$route.params.id,
      method: "get"
    }).then(res => {
      res.data.message.forEach(v => {
        v.msrc = v.src;
        v.w = 640;
        v.h = 480;
      });
      this.imgsInfo = res.data.message;
      console.log(this.imgsInfo);
    });
  }
};
</script>

