<template>
  <div class="news">
    <div class="news-detail">
      <!-- 新闻详情 -->
      <DetailDemo :info="newsInfo"></DetailDemo>
      <!-- 评论 -->
      <Comment :detailid="$route.params.newid"></Comment>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Comment from "@/components/Comment";
import DetailDemo from "@/components/DetailDemo";
export default {
  data() {
    return {
      newsInfo: {}
    };
  },
  created() {
    axios({
      url:
        "http://www.liulongbin.top:3005/api/getnew/" + this.$route.params.newid,
      method: "get"
    }).then(res => {
      console.log(res);
      this.newsInfo = res.data.message[0];
    });
  },
  components: {
    Comment,
    DetailDemo
  }
};
</script>


<style>
.news {
  margin-top: 40px;
  margin-bottom: 60px;
  padding: 5px;
}
</style>