<template>
    <div class="page">
        <!-- <h1>商品描述</h1> -->
        <DetailDemo :info="descInfo"></DetailDemo>
    </div>
</template>

<script>
import DetailDemo from "../../components/DetailDemo";
import axios from "axios";
export default {
  data() {
    return {
      descInfo: {}
    };
  },
  created() {
    axios({
      url:
        "http://www.liulongbin.top:3005/api/goods/getdesc/" +
        this.$route.params.id
    }).then(res=>{
        // console.log(res.data.message[0]);
        this.descInfo = res.data.message[0]
    })
  },
  components: {
    DetailDemo
  }
};
</script>

<style scoped>
.gomeImgLoad {
    width: 100%;
}
</style>
