<template>
    <div>
        <h4>发布评论</h4>
        <hr>
        <textarea name="" id="" cols="30" rows="5" v-model="c_content"></textarea>
        <mt-button type="primary" size="large" @click="addComment">发表评论</mt-button>
        <ul class="mui-table-view">
            <li v-for="(item, index) in comments" :key="index" class="mui-table-view-cell mui-media">
                <a href="javascript:;">
                    <p class="comment-tilte">
                        <span>第{{index + 1}}楼</span>
                        <span>发表时间: {{item.add_time | dateformat("YYYY-MM-DD HH:mm:ss")}}</span>
                        <span class="mui-pull-right">
                            {{item.user_name}}
                        </span>
                    </p>
                    <img class="mui-media-object mui-pull-right" src="../assets/panghu.jpg">
                    <div class="mui-media-body">
                        <p class="mui-ellipsis comment-content">
                            {{item.content}}
                        </p>
                    </div>
                </a>
            </li>
        </ul>
        <!-- 加载更多 -->
        <mt-button type="danger" plain size="large" @click="loadMore" :disabled="noMore">{{btnMsg}}</mt-button>
    </div>
</template>

<script>
import axios from "axios";
import { Toast } from "mint-ui";
import Dateformat from "@/filters/dateformat.js";
export default {
  data() {
    return {
      comments: [],
      pageIndex: 1,
      btnMsg: "加载更多",
      noMore: false,
      c_content: ""
    };
  },
  props: ["detailid"],
  created() {
    this.getComments();
  },
  methods: {
    loadMore() {
      this.pageIndex++;
      this.getComments();
    },
    getComments() {
      axios({
        url:
          "http://www.liulongbin.top:3005/api/getcomments/" +
          this.detailid +
          "?pageindex=" +
          this.pageIndex
      }).then(res => {
        // console.log(res);
        if (res.data.message.length === 0) {
          this.btnMsg = "没有更多数据了";
          this.noMore = true;
          return false;
        }
        this.comments = this.comments.concat(res.data.message);
      });
    },
    addComment() {
      if (this.c_content.trim() === "") {
        Toast("不要调皮!评论信息不能为空哦");
        return false;
      }
      axios({
        url: "http://www.liulongbin.top:3005/api/postcomment/" + this.detailid,
        method: "post",
        data: {
          content: this.c_content
        }
      }).then(res => {
        if (res.data.status == 0) {
          this.comments.unshift({
            add_time: new Date(),
            content: this.c_content,
            user_name: "匿名用户"
          });
          this.c_content = ''
        }
      });
    }
  },
  filters: {
    Dateformat
  }
};
</script>

<style scoped>
    img {
        border-radius: 50%;
    }
</style>




