<template>
  <div class="page">
    <div class="scroll">
      <div class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
        <div class="mui-scroll">
          <router-link class="mymui" :to="'/pics/0'">全部</router-link>
          <router-link class="mymui" :to="'/pics/' + item.id" v-for="(item, index) in categoryList" :key="index">{{item.title}}</router-link>
        </div>
      </div>
    </div>
    <ul>
      <li v-for="(item,index) in picsList" :key="index">
        <router-link :to="'/picdetail/' + item.id">
          <img v-lazy="item.img_url" alt="">
          <div class="info mui-ellipsis-3">
            <h4>{{item.title}}</h4>
            {{item.zhaiyao}}
          </div>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import mui from "@/assets/mui/js/mui.js";

export default {
  data() {
    return {
      categoryList: [],
      picsList: []
    };
  },
  created() {
    axios({
      url: "http://www.liulongbin.top:3005/api/getimgcategory",
      method: "get"
    }).then(res => {
      // console.log(res);

      this.categoryList = res.data.message;
    });
    this.getPicById();
  },
  methods: {
    getPicById() {
      axios({
        url:
          "http://www.liulongbin.top:3005/api/getimages/" +
            this.$route.params.id || 0,
        method: "get"
      }).then(res => {
        console.log(res);
        this.picsList = res.data.message;
      });
    }
  },
  watch: {
    $route: function() {
      this.getPicById();
    }
  },
  mounted() {
    mui(".mui-scroll-wrapper").scroll({
      deceleration: 0.0005
    });
  }
};
</script>



<style scoped>
img[lazy=loading] {
  width: 40px;
  height: 300px;
  margin: auto;
  display: block;
}
.scroll {
  position: relative;
}
ul {
  padding: 10px;
}
ul li {
  list-style: none;
  position: relative;
  margin: 10px 0;
  box-shadow: 0 0 15px #999;
  background-color: #ccc;
}
ul li img {
  width: 100%;
}
ul li .info {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fff;
  font-size: 12px;
  background-color: rgba(0, 0, 0, 0.4);
  max-height: 84px;
}
ul li .info h4 {
  font-size: 13px;
}
.mymui {
  color: inherit;
  display: inline-block;
  width: auto;
  padding: 0 20px;
  line-height: 38px;
  overflow: hidden;
  transition: background-color 0.1s linear;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-decoration: none;
  background: transparent;
}
</style>

