<template>
  <div class="page">
    <div class="goods-list">
      <div class="mui-card">
        <div class="mui-card-content" v-for="(item, index) in cartList" :key="index">
          <div class="mui-card-content-inner">
            <mt-switch v-model="item.isChecked"></mt-switch>
            <img :src="item.thumb_path" class="thumb_img">
            <div class="info">
              <h4>{{item.title}}</h4>
              <div class="box">
                <span class="price">￥{{item.sell_price}}</span>
                <!-- <NumberBox></NumberBox> -->
                <NumberBox v-model="item.count" :max="60" :min="1"></NumberBox>
                <!-- <router-link @click="">删除</router-link> -->
                <mt-button size="small" type="danger" @click="delBtn">删除</mt-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <div class="amount">
            <p>总计（不含运费）</p>
            <p>
              已勾选商品
              <span class="red">{{checkdGoodsNum}}</span> 件 总价：
              <span class="red">￥{{checkdGoodsPrice}}</span>
            </p>
          </div>
          <button class="mint-button mint-button--danger mint-button--normal">
            <!---->
            <label class="mint-button-text">去结算</label>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NumberBox from "@/components/NuberBox.vue";
import axios from "axios";
export default {
  data() {
    return {
      cartList: [],
      delid: 0
    };
  },
  computed: {
    checkdGoodsNum() {
      let num = 0;
      this.cartList.filter(v => v.isChecked).forEach(v => {
        num += v.count;
      });
      return num;
    },
    checkdGoodsPrice() {
      let price = 0;
      this.cartList.filter(v => v.isChecked).forEach(v => {
        price += v.sell_price * v.count;
      });
      return price;
    }
  },
  created() {
    this.getCarList();
  },
  methods: {
    addToCart() {
      this.$store.commit("addToCart", {
        id: this.$route.params.id,
        count: this.goodsNum
      });
    },
    getGoodsIds() {
      let ids = [];
      this.$store.state.cartList.forEach(v => {
        ids.push(v.id);
      });
      let idStr = ids.join() || ""; //id字符串
      return idStr;
    },
    getCarList() {
      if (this.getGoodsIds()) {
        axios({
          url:
            "http://www.liulongbin.top:3005/api/goods/getshopcarlist/" +
            this.getGoodsIds(),
          method: "get"
        }).then(res => {
          res.data.message.forEach(v => {
            v.count = this.$store.state.cartList.find(
              v1 => v1.id == v.id
            ).count;
            v.isChecked = false;
          });
          this.cartList = res.data.message;
          console.log(this.cartList);
        });
      }
    },
    delBtn() {
      console.log(this.delid);
    }
  },
  watch: {
    cartList: {
      handler(value) {
        this.$store.commit("updateCart", this.cartList);
      },
      deep: true
    }
  },
  components: {
    NumberBox
  }
};
</script>



<style scoped>
.mui-card {
  font-size: 14px;
  position: relative;
  overflow: hidden;
  margin: 10px;
  border-radius: 2px;
  background-color: #fff;
  background-clip: padding-box;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}
.mui-card-content {
  font-size: 14px;
  position: relative;
}
.goods-list .mui-card-content-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mint-switch {
  display: flex;
  align-items: center;
  position: relative;
}
.mint-switch-core {
  display: inline-block;
  position: relative;
  width: 52px;
  height: 32px;
  border: 1px solid #d9d9d9;
  border-radius: 16px;
  box-sizing: border-box;
  background: #d9d9d9;
}
.goods-list .mui-card-content-inner .thumb_img {
  width: 60px;
  height: 60px;
}
.goods-list .mui-card-content-inner .info {
  flex: 1;
}
.goods-list .mui-card-content-inner .info .box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.goods-list .mui-card-content-inner .info h4 {
  font-size: 14px;
}
.goods-list .mui-card-content-inner .info .box .price {
  font-size: 16px;
  color: red;
}
.mui-numbox-cms {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: 120px;
  height: 35px;
  padding: 0 40px;
  vertical-align: middle;
  border: 1px solid #bbb;
  border-radius: 3px;
  background-color: #efeff4;
}
.mui-card-content-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mui-card-content-inner {
  position: relative;
  padding: 15px;
}
.mui-card-content-inner .red {
  color: red;
  font-size: 16px;
  font-weight: 700;
}
</style>
