import Vue from 'vue'
import Router from 'vue-router'

// 分类
import Home from './views/tabbar/Home.vue'
import Member from './views/tabbar/Member.vue'
import Cart from './views/tabbar/Cart.vue'
import Search from './views/tabbar/Search.vue'

// 新闻
import NewsList from './views/news/List.vue'
import NewsDetail from './views/news/Detail.vue'

// 图片
import Pics from './views/pics/Pic.vue'
import Picdetail from './views/pics/Picdetail.vue'
import PicdetailImgs from './views/pics/PicdetailImgs.vue'

// 商品
import GoodsList from './views/goods/List.vue'
import GoodsDetail from './views/goods/Detail.vue'
import GoodsDesc from './views/goods/Description.vue'
import GoodsComments from './views/goods/comments.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      component: Home
    },
    {
      path: '/member',
      component: Member
    },
    {
      path: '/cart',
      component: Cart
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/newslist',
      component: NewsList
    },
    {
      path: '/newsdetails/:newid',
      component: NewsDetail
    },
    {
      path: '/pics/:id',
      component: Pics
    },
    {
      path: '/picdetail/:id',
      component: Picdetail,
      children: [{
        path: '/',
        component: PicdetailImgs,
      }]
    },
    {
      path: '/goodslist',
      component: GoodsList
    },
    {
      path: '/goodsdetail/:id',
      component: GoodsDetail
    },
    {
      path: '/goodsdesc/:id',
      component: GoodsDesc
    },
    {
      path: '/goodscomments/:id',
      component: GoodsComments
    }
  ]
})