<template>
    <div class="page">
        <div class="item" v-for="(item, index) in goodsList" :key="index">
            <router-link :to="'/goodsdetail/'+ item.id">
                <img :src="item.img_url" alt="">
                <h4>{{item.title}}</h4>
                <div class="info">
                    <p class="price">
                        <span class="nowPrice">￥{{item.sell_price}}</span>
                        <span>
                            <del>￥{{item.market_price}}</del>
                        </span>
                    </p>
                    <p class="sale">
                        <span>热卖中</span>
                        <span>剩{{item.stock_quantity}}件</span>
                    </p>
                </div>
            </router-link>
        </div>
        <mt-button plain size="large" type="danger" :disabled="noMore" @click="loadMore">{{btnMsg}}</mt-button>
    </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      goodsList: [],
      pageindex: 1,
      noMore: false,
      btnMsg: '加载更多'
    };
  },
  created() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      axios({
        url:
          "http://www.liulongbin.top:3005/api/getgoods?pageindex=" +
          this.pageindex,
        method: "get"
      }).then(res => {
        if (res.data.message.length === 0) {
          this.btnMsg = "没有更多数据了";
          this.noMore = true;
          return false;
        }
        console.log(res.data.message);
        this.goodsList = this.goodsList.concat(res.data.message);
      });
    },
    loadMore() {
      this.pageindex++;
      this.getInfo();
    }
  }
};
</script>



<style scoped>
.page {
  display: flex;
  flex-wrap: wrap;
  padding: 3px;
}
.item {
  width: 48%;
  border: 1px solid #ccc;
  margin: 1%;
  box-shadow: 0 0 7px #aaa;
  padding: 2px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.item h4 {
  font-size: 14px;
  color: #8f8f94;
}
.item img {
  width: 100%;
}
.item .info {
  background-color: #eee;
}
.item .info .price {
  margin: 0;
  margin-top: 10px;
  font-size: 13px;
  color: #8f8f94;
}
.item .info .price .nowPrice {
  color: red;
  font-size: 15px;
  margin-right: 8px;
}
.item .info .sale {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-top: 10px;
  font-size: 13px;
  color: #8f8f94;
}
</style>
