<template>
  <div class="page ballPosition">
    <!-- 商品轮播图 -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <mt-swipe :auto="2000">
            <mt-swipe-item v-for="(item, index) in goodsImgs" :key="index">
              <img :src="item.src" alt="">
            </mt-swipe-item>
          </mt-swipe>
        </div>
      </div>
    </div>
    <!-- 商品价格 -->
    <div class="mui-card">
      <div class="mui-card-header">{{goodsInfo.title}}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <div class="price">
            <span>市场价：
              <del>￥{{goodsInfo.market_price}}</del>
            </span>&nbsp;&nbsp;&nbsp;&nbsp;
            <span>销售价：
              <span class="sale_price">￥{{goodsInfo.sell_price}}</span>
            </span>
          </div>
          <div class="count">
            <span>购买数量：</span>
            <NumberBox v-model="goodsNum" :max="60" :min="1"></NumberBox>
          </div>
          <div class="btns">
            <button class="mint-button mint-button--primary mint-button--normal">
              <!---->
              <label class="mint-button-text">立即购买</label>
            </button>
            <button @click="addToCart" class="mint-button mint-button--danger mint-button--normal">
              <!---->
              <label class="mint-button-text">加入购物车</label>
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- 商品参数 -->
    <div class="mui-card">
      <div class="mui-card-header">商品参数</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>商品货号：{{goodsInfo.goods_no}}</p>
          <p>库存情况：{{goodsInfo.stock_quantity}}件</p>
          <p>上架时间：{{goodsInfo.add_time | Dateformat("YYYY-MM-DD HH:mm:ss")}}</p>
        </div>
      </div>
      <div class="mui-card-footer">

        <router-link :to="'/goodsdesc/'+ goodsInfo.id" class="mint-button mint-button--primary mint-button--large is-plain">
          <label class="mint-button-text">商品描述</label>
        </router-link>
        <br>
        <router-link :to="'/goodscomments/'+ goodsInfo.id" class="mint-button mint-button--danger mint-button--large is-plain">
          <label class="mint-button-text">商品评论</label>
        </router-link>
      </div>
    </div>
    <!-- 小球 -->
    <transition @before-enter="beforeEnter" @enter="enter">
      <div class="readBall" v-show="isShowBall"></div>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
import Dateformat from "@/filters/dateformat.js";
import NumberBox from "@/components/NuberBox.vue";
export default {
  data() {
    return {
      goodsInfo: {},
      goodsImgs: [],
      goodsNum: 0,
      isShowBall: false
    };
  },
  created() {
    axios({
      //商品信息
      url:
        "http://www.liulongbin.top:3005/api/goods/getinfo/" +
        this.$route.params.id,
      method: "get"
    }).then(res => {
      // console.log(res.data.message[0]);
      this.goodsInfo = res.data.message[0];
    });
    axios({
      //商品图片
      url:
        "http://www.liulongbin.top:3005/api/getthumimages/" +
        this.$route.params.id,
      method: "get"
    }).then(res => {
      //   console.log(res.data.message);
      this.goodsImgs = res.data.message;
    });
  },
  methods: {
    addToCart() {
      this.$store.commit("addToCart", {
        id: this.$route.params.id,
        count: this.goodsNum
      });
      this.isShowBall = true;
    },
    beforeEnter(el) {
      el.style.top = 400 + "px";
      el.style.left = 145 + "px";
      el.style.transition = "all .3s";
    },
    enter(el) {
      el.offsetWidth;
      el.style.top = 715 + window.scrollY + "px";
      el.style.left = 240 + "px";
      this.isShowBall = false;
    }
  },
  filters: {
    Dateformat
  },
  components: {
    NumberBox
  }
};
</script>



<style scoped>
.ballPosition {
  position: relative;
}
.readBall {
  width: 20px;
  height: 20px;
  background-color: red;
  border-radius: 50%;
  position: absolute;
  /* left: 39%;
  top: 50%; */
  z-index: 999;
}
.mint-button {
  padding-top: 10px;
}
.mint-swipe {
  height: 230px;
}
.mint-swipe img {
  height: 180px;
}
.mint-swipe-item {
  text-align: center;
}
.mui-card {
  font-size: 14px;
  position: relative;
  overflow: hidden;
  margin: 10px;
  border-radius: 2px;
  background-color: #fff;
  background-clip: padding-box;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}
.mui-card-content-inner > div {
  position: relative;
  margin: 10px 0;
}
.mint-button--normal,
.mint-button--small {
  display: inline-block;
  padding: 0 12px;
}
.sale_price {
  color: red;
  font-size: 18px;
}
.mui-numbox-cms {
  position: relative;
  display: inline-block;
  overflow: hidden;
  width: 120px;
  height: 35px;
  padding: 0 40px;
  vertical-align: middle;
  border: 1px solid #bbb;
  border-radius: 3px;
  background-color: #efeff4;
}
.mui-card-footer {
  display: block;
}
.mint-button--large {
  display: block;
  width: 100%;
}
</style>
